<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query">
            <el-button slot="append" icon="el-icon-search" @click="getOrderlist"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!-- table -->
      <el-table
        :data="orderlist"
        stripe
        border>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <!--  -->
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.pay_status === '1'" type="success"></el-tag>
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <!--  -->
        <el-table-column label="">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat}}
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="">
          <template slot-scope="">
            <el-button @click="addressDialogVisible = true" size="mini" type="primary" class="el-icon-edit"></el-button>
            <el-button @click="showProgressDialog" size="mini" type="success" class="el-icon-location"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 15, 20, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressDialogVisible"
      width="50%">
      <el-form
        :rules="addressFormRules"
        ref="addressFormRef"
        :model="addressForm"
        label-width="100px">
        <!-- / -->
        <el-form-item label="/" prop="address1">
          <el-cascader
            v-model="addressForm.address1"
            :options="citydata"
            :props="{ expandTrigger: 'hover' }"
            @change="handleCityChange"></el-cascader>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <!-- TODO:  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
        <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressDialogVisible"
      width="50%">
      <!--  -->
      <el-timeline :reverse="false">
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import citydata from './citydata.js'
import { data } from './progressInfo.json'
export default {
  name: 'order',
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
        // user_id: '',
        // pay_status: '',
        // is_send: '',
        // order_fapiao_title: [],
        // order_fapiao_company: '',
        // order_fapiao_content: '',
        // consignee_addr: ''
      },
      // 
      total: 0,
      // 
      orderlist: [],
      // 
      addressDialogVisible: false,
      // 
      progressDialogVisible: false,
      // 
      addressForm: {
        address1: [],
        address2: ''
      },
      // 
      addressFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      citydata,
      // 
      progressInfo: data
    }
  },
  created() {
    this.getOrderlist()
  },
  methods: {
    async getOrderlist() {
      // 
      const { data: res } = await this.$http.get('/orders', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      // 
      this.orderlist = res.data.goods
      this.total = res.data.total
      // console.log(res.data)
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderlist()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderlist()
    },
    // TODO: 
    handleCityChange() {

    },
    // 
    async showProgressDialog() {
      // 
      // const { data: res } = await this.$http.get('/kuaidi/804909574412544580')
      // if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // this.$message.success(res.meta.msg)
      // // 
      // this.progressInfo = res.data
      // console.log(res.data)
      // 
      this.progressDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
